<template>
  <div>
    <!--  -->
    <nav-bar class="home-nav">
      <div slot="left" class="lefts" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" alt />
      </div>
      <div slot="center" class="header">
        <img src="@/styles/img/4_03.png" alt />
      </div>
    </nav-bar>

    <div class="menu">
      <a href="detail.html">
        <span></span>
      </a>
      <a href="comment.html">
        <span style="color: #000"></span>
      </a>
      <div class="so"></div>
    </div>

    <div class="imag">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" @change="handleChange">
        <van-swipe-item v-for="(item,index) in banner" :key="index">
          <img :src="item" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="number">
      {{now}}
      <span>/{{banner.length}}</span>
    </div>

    <div class="main">
      <div class="title">
        BOSC XISTERA {{goods.name}}
        <div class="xian"></div>
      </div>
      <div class="titlebox">
        <div class="xian"></div>
        <div class="titlemain">
          <div class="left"></div>
          <div class="right"></div>
        </div>
        <div class="titlemain">
          <div class="left"></div>
          <div
            class="right"
            v-for="(item,index) in message"
            :key="index"
          >{{item.name}}:{{item.value}}</div>
        </div>
        <div class="titlemain">
          <div class="left"></div>
          <div class="right">
            <ul class="main-hot-pro">
              <li
                v-for="(v,index) in colorData"
                :key="index"
                :style="{'border-color':v.value,background:activeColor===index ? v.value:''}"
                @click="hadleActiveColor(index)"
              ></li>
            </ul>
          </div>
        </div>
        <div class="titlemain">
          <div class="left"></div>
          <div class="right" v-for="item in templateT" :key="item.index">{{item.areas}}</div>
        </div>
      </div>
      <div class="titlebox2">
        <div class="num">
          <span>{{goods.price | currency}}</span>RMB
        </div>
        <div class="left"></div>
        <div class="jis">
          <div class="jisleft" @click="handleDec">-</div>
          {{count}}
          <div class="jisright" @click="handleAdd">+</div>
        </div>
      </div>
      <div class="title2">
        <div class="xian"></div>
        <span></span>
      </div>
      <div class="title2-main">
        <ul class="mainbox">
          <li v-for="item in viewHistoryGoods" :key="item.id">
            <router-link :to="{}">
              <img :src="item.thumb" />
              <div class="line"></div>
              <h4>VERFO LAB</h4>
              <h2>{{item.name}}</h2>
              <h6>
                {{item.price | currency}}
                <span>RMB</span>
              </h6>
              <a href class="buy-now">
                <i class="iconfont icon-jia"></i>
                <span></span>
              </a>
            </router-link>
          </li>
        </ul>
      </div>
    </div>

    <div class="detail">
      <div class="detail-main">
        <div class="xian1"></div>
        <div class="xian2"></div>
        <div class="xian3"></div>
        <h6>COMMODITY DETAILS</h6>
        <h3>
          <span></span>
          <span></span>
        </h3>
        <div v-html="contents.content" class="content-content"></div>
      </div>
    </div>
    <div class="ser">
      <div class="xian1"></div>
      <div class="xian2"></div>
      <div class="xian3"></div>
      <h6>SERVICE GUARANTEE</h6>
      <h3>
        
        <span></span>
      </h3>
      <ul class="sea">
        <div class="xian"></div>
        <div class="xian"></div>
        <div class="xian"></div>
        <li>
          <img src="@/styles/img/xy3.png" alt />
          <div></div>
        </li>
        <li>
          <img src="@/styles/img/xy4.png" alt />
          <div></div>
        </li>
        <li>
          <img src="@/styles/img/xy5.png" alt />
          <div></div>
        </li>
        <li>
          <img src="@/styles/img/xy6.png" alt />
          <div></div>
        </li>
      </ul>
    </div>
    <!--  -->
    <section>
      <goods-list :goods="goodsList" title=""></goods-list>
    </section>
    <div class="foo">
      <div class="foo-left">
        <i class="iconfont icon-shoucang"></i>
        <div></div>
      </div>
      <div class="foo-center" @click="handleCart()"></div>
      <div class="foo-right" @click="handleBuyNow"></div>
    </div>
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      :goods-id="gid"
      :quota="0"
      :hide-stock="true"
      @buy-clicked="handelBuyClicked"
      @add-cart="onAddCartClicked"
    />
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import GoodsList from "components/common/goodsList/GoodsList";
import {mapState,mapMutations} from "vuex";
import qs from "qs";
export default {
  name: "Details",
  data() {
    return {
      gid: 0,
      page: 1,
      pageSize: 6,
      contents: {},
      banner: [],
      now: 1,
      goods: {},
      message: [],
      // 
      colorData: [],
      activeColor: "",
      // 
      goodsList: [],
      // 
      templateT: [],
      // id
      viewHistory: [],
      // 
      viewHistoryGoods: [],
      count: 1, // 
      // 
      show: false,
      // sku
      sku: {
        // sku
        // 
        tree: [
          {
            k: "", // skuKeyName
            v: [
              {
                id: "30349", // skuValueId id
                name: "", // skuValueName
                imgUrl: "", // 
                previewImgUrl: "" // 
              },
              {
                id: "1215",
                name: "",
                imgUrl: "https://img.yzcdn.cn/2.jpg",
                previewImgUrl: "https://img.yzcdn.cn/2p.jpg"
              }
            ],
            k_s: "s1" // skuKeyStrsku  list key value  id
          }
        ],
        //  sku M  sku S 
        list: [
          {
            id: 2259, // skuId
            price: 100, // 
            s1: "1215", //  k_s  s1  id
            s2: "1193", //  k_s  s2  id
            s3: "0", // 30
            stock_num: 110 //  sku 
          }
        ],
        price: "1.00", // 
        stock_num: 227, // 
        collection_id: 2261, //  skuId  collection_id sku  id
        none_sku: false, // 
        messages: [
          {
            // 
            datetime: "0", //  time '1' 
            multiple: "0", //  text '1' 
            name: "", // 
            type: "text", // : id_no, text, tel, date, time, email
            required: "1", //  '1' 
            placeholder: "" // 
          }
        ],
        hide_stock: false // 
      },
      // 
      hasAddToCart:false
    };
  },
  computed: {
    ...mapState(['cart'])
  },
  components: {
    NavBar,
    GoodsList
  },
  created() {},
  mounted() {
    this.gid = this.$route.query.gid;
    console.log(this.contents);
    
    // 
    let obj =this.cart.find(v=>v.id*1===this.gid);
      if(obj){
        
        this.count = obj.count;
        this.activeColor = obj.activeColor;
        this.hasAddToCart = true;
      }
      
    this.fetchContentData();
    this.fetchGoodsColor();
    this.fetchDetailData();
    this.fetchGoodsData();
    this.fetchTemplate();
    if (localStorage.getItem("viewHistory")) {
      this.viewHistory = JSON.parse(localStorage.getItem("viewHistory")).filter(
        v => v !== this.gid * 1 // 
      );
    }
    this.fetchHistory(); // 
  },
  beforeRouteLeave(to, from, next) {
    // 
    let index = this.viewHistory.indexOf(this.gid * 1);
    if (index != -1) {
      this.viewHistory.splice(index, 1);
    }
    // 
    this.viewHistory.unshift(this.gid * 1);
    localStorage.setItem("viewHistory", JSON.stringify(this.viewHistory));
    next();
  },
  beforeRouteUpdate(to, from, next) {
    // 
    let index = this.viewHistory.indexOf(this.gid * 1);
    if (index != -1) {
      this.viewHistory.splice(index, 1);
    }
    // 
    this.viewHistory.unshift(this.gid * 1);
    localStorage.setItem("viewHistory", JSON.stringify(this.viewHistory));

    this.banner = [];
    this.message = [];
    this.colorData = [];
    this.goodsList = [];
    this.viewHistory = [];
    this.viewHistoryGoods = [];
    (this.templateT = []), (this.now = 0);
    this.gid = to.query.gid;
    // 
    this.hasAddToCart = false;
    this.fetchContentData();
    this.fetchGoodsColor();
    this.fetchDetailData();
    this.fetchGoodsData();
    this.fetchTemplate();

        // 
    let obj =this.cart.find(v=>v.id*1===this.gid);
      if(obj){
        
        this.count = obj.count;
        this.activeColor = obj.activeColor;
        this.hasAddToCart = true;
      }

    if (localStorage.getItem("viewHistory")) {
      this.viewHistory = JSON.parse(localStorage.getItem("viewHistory")).filter(
        v => v !== this.gid * 1 // 
      );
    }
    this.fetchHistory(); // 
    this.$nextTick(() => {
      document.documentElement.scrollTop = 0;
    });
    next();
  },
  methods: {
    ...mapMutations(["addToCart","removeFromCart"]),
    // 
    handleCart(buyNow=false){
      // this.show = true;
      if(this.activeColor===""){
        this.$toast('');
        return;
      }
      //  
      if(this.hasAddToCart){
        this.removeFromCart(this.gid);
      }
      this.hasAddToCart = true;
      let obj ={};
      obj.id = this.gid;
      obj.thumb = this.goods.thumb;
      obj.name = this.goods.name;
      obj.price = this.goods.special_price;
      obj.sku = this.colorData[this.activeColor];
      obj.count = this.count;
      obj.activeColor = this.activeColor;


      obj.tid = this.contents.did;


      if(buyNow){
        obj.checked=true;
      }else{
        obj.checked=false
      }
      this.addToCart(obj)
      this.$toast('');
    },
    handleBuyNow(){
      this.handleCart();
      this.$router.push({name:"cart"})
    },
    // 
    handelBuyClicked() {},
    onAddCartClicked() {},
    // 
    handleDec() {
      if (this.count === 1) {
        return;
      }
      this.count--;
    },
    handleAdd() {
      if (this.activeColor === "") {
        this.$toast("");
        return;
      }
      if (this.count === this.colorData[this.activeColor].store) {
        this.$toast("");
        return;
      }
      this.count++;
    },
    hadleActiveColor(index) {
      this.activeColor = index;
      this.count = 1;
    },
    // 
    handleChange(index) {
      this.now = index + 1;
    },
    // 
    async fetchDetailData() {
      let res = await this.$http.get(this.$api.COLORS, {
        gid: this.gid
      });
      //  console.log(res);
      this.colorData = res.data.data;
    },
    async fetchGoodsColor() {
      let res = await this.$http.get(this.$api.GOODS + "/" + this.gid);
      //  console.log(res);
      this.goods = res.data.data;
    },
    // 
    async fetchContentData() {
      let res = await this.$http.get(this.$api.DETAIL, {
        gid: this.gid
      });
      this.contents = res.data.data[0];
      this.banner = this.contents.banner.split(";");
      this.message = qs.parse(this.contents.message);
      console.log(this.contents);
      
    },
    // 
    async fetchGoodsData() {
      let res = await this.$http.get(this.$api.GOODS, {
        state: 1,
        page: this.page,
        pageSize: this.pageSize,
        recommend: 1
      });
      // 
      this.goodsList = res.data.data.filter(v => v.id != this.gid);
    },
    // 
    async fetchTemplate() {
      let res = await this.$http.get(this.$api.DDETAIL, {
        did: this.gid,
        page: 1,
        pageSize: 100
      });
      console.log(res);
      this.templateT = res.data.data;
    },
    // 
    fetchHistory() {
      this.viewHistory.forEach(async id => {
        let res = await this.$http.get(this.$api.GOODS + "/" + id);
        this.viewHistoryGoods.push(res.data.data);
        console.log(this.viewHistoryGoods);
      });
    }
  }
};

// 
// ,background:activeColor===index ? v.value:''
</script>
<style lang='scss' scoped>
@import "styles/detail";
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
  height: 100%;
  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}
.nav-bar {
  position: fixed;
  top: 0;
  // left: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
}
.header img {
  width: 1.94rem;
  height: 0.4rem;
  margin-top: 0.24rem;
}
.lefts img {
  width: 0.6rem;
  height: 0.4rem;
  position: absolute;
  top: 0.15rem;
  left: 0.1rem;
}
.main-hot-pro li {
  width: 0.26rem;
  height: 0.26rem;
  border-radius: 50%;
  align-self: center;
  display: inline-block;
  margin-left: 0.1rem;
}
.number {
  float: right;
  margin-top: 0.4rem;
  margin-right: 0.3rem;
  color: rgb(255, 123, 0);
  font-weight: 600;
  font-size: 14px;
}
.main-hot-pro li {
  width: 0.26rem;
  height: 0.26rem;
  border-radius: 50%;
  border: 4px solid red;
  align-self: center;
  display: inline-block;
  margin-left: 0.1rem;
}
.content-content {
  margin-top: 0.5rem;
}
</style>
